/* Typographical Elements
--------------------------------------------- */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,700;1,700&display=swap');
html {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

body {
    background-color: $body-bkg;
    color: $base-color;
    font-family: $font-family;
    font-size: $font-size;
    font-weight: 400;
    line-height: 1.625;
    margin: 0;
    overflow-x: hidden;
}
.container{
    max-width: $max-width;
    padding: 0;
    @media (max-width: $padCont) {
        padding: 0 50px;
    }
}
button,
input:focus,
input[type="button"],
input[type="reset"],
input[type="submit"],
textarea:focus,
.button,
.gallery img {
    transition: all 0.2s ease-in-out;
}

a {
    color: #0073e5;
    text-decoration: none;
    transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
    &:focus,
    &:hover {
        color: #333;
        text-decoration: none;
    }
}

p {
    margin: 0 ;
    padding: 0;
}
span{
    color: $color-blue;
}
ol,
ul {
    margin: 0;
    padding: 0;
}

li {
    list-style-type: none;
}

hr {
    border: 0;
    border-collapse: collapse;
    border-bottom: 1px solid rgba($base-color,0.5);
    clear: both;
    color: $base-color;
    margin: 0 auto;
}

b,
strong {
    font-weight: 700;
}
i{
    padding-left: 15px;
}
blockquote,
cite,
em,
i {
    font-style: italic;
}

mark {
    background: #ddd;
    color: #333;
}

blockquote {
    margin: 30px;
}


/* Headings
--------------------------------------------- */

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $font-heading;
    font-weight: 500;
    line-height: 1.2;
    margin: 0 ;
}

h1 {
    color: $color-white;
    font-size: 64px;
    font-weight: 700;
}

h2 {
    font-size: 40px;
    color: rgb(35, 31, 32);
}

h3 {
    font-size: 24px;
}

h4 {
    font-size: 45px;
    color: $color-white;
    text-align: center;
    padding-bottom: 60px;
}

.entry-content h3,
.entry-content h4 {
    font-weight: 600;
}

.entry-content h4 {
    margin-top: 40px;
}

h5 {
    font-size: 35px;
    color: $color-white;
}

h6 {
    font-size: 20px;
    color: rgb(13, 72, 108);
}


/* Objects
--------------------------------------------- */

embed,
iframe,
img,
object,
video,
.wp-caption {
    max-width: 100%;
}

img {
    height: auto;
    vertical-align: top;
}

figure {
    margin: 0;
}


/* Gallery
--------------------------------------------- */

.gallery {
    overflow: hidden;
}

.gallery img {
    border: 1px solid #eee;
    height: auto;
    padding: 4px;
}

.gallery img:focus,
.gallery img:hover {
    border: 1px solid #999;
    outline: none;
}

.gallery-columns-1 .gallery-item {
    width: 100%;
}

.gallery-columns-2 .gallery-item {
    width: 50%;
}

.gallery-columns-3 .gallery-item {
    width: 33%;
}

.gallery-columns-4 .gallery-item {
    width: 25%;
}

.gallery-columns-5 .gallery-item {
    width: 20%;
}

.gallery-columns-6 .gallery-item {
    width: 16.6666%;
}

.gallery-columns-7 .gallery-item {
    width: 14.2857%;
}

.gallery-columns-8 .gallery-item {
    width: 12.5%;
}

.gallery-columns-9 .gallery-item {
    width: 11.1111%;
}

.gallery-columns-2 .gallery-item:nth-child(2n+1),
.gallery-columns-3 .gallery-item:nth-child(3n+1),
.gallery-columns-4 .gallery-item:nth-child(4n+1),
.gallery-columns-5 .gallery-item:nth-child(5n+1),
.gallery-columns-6 .gallery-item:nth-child(6n+1),
.gallery-columns-7 .gallery-item:nth-child(7n+1),
.gallery-columns-8 .gallery-item:nth-child(8n+1),
.gallery-columns-9 .gallery-item:nth-child(9n+1) {
    clear: left;
}

.gallery-item {
    float: left;
    margin: 0 0 30px;
    text-align: center;
}


/* Screen Reader Text
--------------------------------------------- */

.screen-reader-shortcut,
.screen-reader-text,
.screen-reader-text span {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    overflow: hidden;
    position: absolute !important;
    width: 1px;
    word-wrap: normal !important;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus,
.widget_search input[type="submit"]:focus {
    background: #fff;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #333;
    display: block;
    font-size: 1em;
    font-weight: 700;
    height: auto;
    padding: 15px 23px 14px;
    text-decoration: none;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */
}

.more-link {
    display: inline-block;
    position: relative;
    margin-bottom: 30px;
}


/* Structure and Layout
---------------------------------------------------------------------------- */


/* Site Container
--------------------------------------------- */

.site-container {
    animation: fadein 1s;
    word-wrap: break-word;
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}


/* Site Inner
--------------------------------------------- */

.site-inner {
    clear: both;
    margin: 0 auto;
    padding: 60px 30px 0;
}


/* Common Classes
---------------------------------------------------------------------------- */


/* Genesis
--------------------------------------------- */

.after-entry,
.archive-description,
.author-box {
    margin-bottom: 40px;
}

.after-entry {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
    padding: 20px 30px;
}

.after-entry .widget:last-of-type {
    margin-bottom: 0;
}

.breadcrumb {
    border-bottom: 1px solid #eee;
    font-size: 16px;
    margin-bottom: 40px;
    padding-bottom: 10px;
}

.genesis-title-hidden .breadcrumb {
    margin-top: 40px;
}

.archive-description p:last-child,
.author-box p:last-child {
    margin-bottom: 0;
}


/* Titles
--------------------------------------------- */

.archive-description .entry-title,
.archive-title,
.author-box-title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 10px;
}

.entry-title {
    font-size: 30px;
    margin-bottom: 10px;
}

.entry-title a,
.sidebar .widget-title a {
    color: #333;
    text-decoration: none;
}

.entry-title a:focus,
.entry-title a:hover {
    color: $link-color;
}

.widget-title {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 20px;
}

.genesis-title-hidden .site-inner {
    padding-top: 0;
}


/* WordPress
--------------------------------------------- */

a.aligncenter img {
    display: block;
    margin: 0 auto;
}

a.alignnone {
    display: inline-block;
}

.alignleft {
    float: left;
    text-align: left;
}

.alignright {
    float: right;
    text-align: right;
}

a.alignleft,
a.alignnone,
a.alignright {
    max-width: 100%;
}

img.centered,
.aligncenter,
.singular-image {
    display: block;
    margin: 0 auto 30px;
}

img.alignnone,
.alignnone {
    margin-bottom: 15px;
}

a.alignleft,
img.alignleft,
.wp-caption.alignleft {
    margin: 0 20px 20px 0;
}

a.alignright,
img.alignright,
.wp-caption.alignright {
    margin: 0 0 20px 20px;
}

figcaption,
.gallery-caption,
.wp-caption-text {
    font-size: 14px;
    font-weight: 600;
    margin-top: 0.5em;
    margin-bottom: 1em;
    padding-left: 10px;
    padding-right: 10px;
    text-align: center;
}

.entry-content p.wp-caption-text {
    margin-bottom: 0;
}

.entry-content .wp-audio-shortcode,
.entry-content .wp-playlist,
.entry-content .wp-video {
    margin: 0 0 30px;
}


/* Widgets
---------------------------------------------------------------------------- */

.widget {
    margin-bottom: 40px;
}

.widget p:last-child,
.widget ul>li:last-of-type {
    margin-bottom: 0;
}

.widget ul>li {
    margin-bottom: 10px;
}

.widget ul>li:last-of-type {
    padding-bottom: 0;
}

.widget ol>li {
    list-style-position: inside;
    list-style-type: decimal;
    padding-left: 20px;
    text-indent: -20px;
}

.widget li li {
    border: 0;
    margin: 0 0 0 30px;
    padding: 0;
}

.widget_calendar table {
    width: 100%;
}

.widget_calendar td,
.widget_calendar th {
    text-align: center;
}


/* Featured Content
--------------------------------------------- */

.featured-content .entry {
    border-bottom: 1px solid #eee;
    margin-bottom: 20px;
}

.featured-content .entry:last-of-type {
    border-bottom: none;
    margin-bottom: 0;
}

.featured-content .entry-title {
    font-size: 16px;
    margin-bottom: 5px;
    margin-top: 10px;
}


/* Site Header
---------------------------------------------------------------------------- */

.site-header {
    background-color: #fff;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
    padding: 0 30px;
}


/* Title Area
--------------------------------------------- */

.title-area {
    float: left;
    padding-bottom: 25px;
    padding-top: 25px;
}

.wp-custom-logo .title-area {
    max-width: 350px;
    padding-bottom: 5px;
    padding-top: 5px;
    width: 100%;
}

.wp-custom-logo .title-area img {
    width: auto;
}

.site-title {
    font-size: 20px;
    font-weight: 600;
    line-height: 1;
    margin-bottom: 0;
}

.site-title a,
.site-title a:focus,
.site-title a:hover {
    color: #333;
    text-decoration: none;
}

.site-description,
.wp-custom-logo .site-title {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    overflow: hidden;
    position: absolute !important;
    width: 1px;
    word-wrap: normal !important;
}


/* Site Navigation
---------------------------------------------------------------------------- */

.genesis-nav-menu {
    clear: both;
    line-height: 1;
    width: 100%;
}

.genesis-nav-menu .menu-item {
    display: block;
    float: none;
    position: relative;
}

.genesis-nav-menu a {
    color: #333;
    display: block;
    font-size: 15px;
    font-weight: 400;
    outline-offset: -1px;
    padding-bottom: 12px;
    padding-top: 12px;
    text-decoration: none;
}

.genesis-nav-menu a:focus,
.genesis-nav-menu a:hover,
.genesis-nav-menu .current-menu-item>a,
.genesis-nav-menu .sub-menu .current-menu-item>a:focus,
.genesis-nav-menu .sub-menu .current-menu-item>a:hover {
    color: #0073e5;
    text-decoration: none;
}

.genesis-nav-menu .sub-menu,
.genesis-nav-menu .sub-menu a {
    width: 100%;
}

.genesis-nav-menu .sub-menu {
    clear: both;
    display: none;
    left: -9999px;
    margin: 0;
    opacity: 1;
    padding-left: 15px;
    position: static;
    z-index: 99;
}

.genesis-nav-menu .sub-menu a {
    background-color: #fff;
    font-size: 14px;
    position: relative;
    word-wrap: break-word;
}

.genesis-nav-menu .menu-item:focus,
.genesis-nav-menu .menu-item:hover {
    position: relative;
}

.genesis-nav-menu .menu-item:hover>.sub-menu {
    display: block;
    left: auto;
    opacity: 1;
}


/* Responsive Menu
--------------------------------------------- */

.menu .menu-item:focus {
    position: static;
}

.menu .menu-item>a:focus+ul.sub-menu,
.menu .menu-item.sfHover>ul.sub-menu {
    left: auto;
    opacity: 1;
}

.genesis-responsive-menu {
    display: none;
    position: relative;
}

.genesis-responsive-menu .genesis-nav-menu .menu-item:hover>.sub-menu {
    display: none;
}

.menu-toggle,
.sub-menu-toggle {
    background-color: transparent;
    border-width: 0;
    color: #333;
    display: block;
    margin: 0 auto;
    overflow: hidden;
    text-align: center;
    visibility: visible;
}

.menu-toggle:focus,
.menu-toggle:hover,
.sub-menu-toggle:focus,
.sub-menu-toggle:hover {
    background-color: transparent;
    border-width: 0;
    color: #0073e5;
}

.menu-toggle {
    float: right;
    line-height: 20px;
    margin-bottom: 10px;
    margin-top: 10px;
    padding: 15px 0;
    position: relative;
    z-index: 1000;
}

.menu-toggle.activated::before {
    content: "\f335";
}

.site-header .menu-toggle::before {
    float: left;
    margin-right: 5px;
    position: relative;
    text-rendering: auto;
    top: 1px;
}

.sub-menu-toggle {
    float: right;
    padding: 9px 10px;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 100;
}

.sub-menu .sub-menu-toggle {
    padding: 12px 10px;
}

.sub-menu-toggle::before {
    display: inline-block;
    text-rendering: auto;
    transform: rotate(0);
    transition: transform 0.25s ease-in-out;
}

.sub-menu-toggle.activated::before {
    transform: rotate(180deg);
}


/* Header Menu
--------------------------------------------- */

.nav-primary {
    clear: left;
    padding-bottom: 15px;
    padding-top: 15px;
    width: 100%;
}


/* Footer Menu
--------------------------------------------- */

.nav-secondary {
    margin-top: 10px;
}

.nav-secondary .genesis-nav-menu {
    line-height: 1.5;
}

.nav-secondary .menu-item {
    display: inline-block;
}

.nav-secondary a {
    margin-left: 10px;
    margin-right: 10px;
    padding: 0;
}


/* Content Area
---------------------------------------------------------------------------- */


/* Entry Content
--------------------------------------------- */

.entry {
    margin-bottom: 40px;
}

.entry-content ol,
.entry-content ul {
    margin-bottom: 30px;
    padding-left: 40px;
}

.entry-content ol>li {
    list-style-type: decimal;
}

.entry-content ul>li {
    list-style-type: disc;
}

.entry-content ol ul>li,
.entry-content ul ul>li {
    list-style-type: circle;
}

.entry-content ol ol,
.entry-content ul ul {
    margin-bottom: 0;
}

.entry-content code {
    background-color: #f5f5f5;
}

.content .sticky {
    background-color: #f5f5f5;
    padding: 30px;
}


/* Entry Meta
--------------------------------------------- */

p.entry-meta {
    font-size: 16px;
    margin-bottom: 0;
}

.entry-header .entry-meta {
    margin-bottom: 20px;
}

.entry-footer .entry-meta {
    border-top: 1px solid #eee;
    padding-top: 20px;
}

.entry-categories,
.entry-tags {
    display: block;
}

.entry-comments-link::before {
    content: "\2014";
    margin: 0 6px 0 2px;
}


/* Pagination
--------------------------------------------- */

.pagination {
    clear: both;
    margin: 60px 0;
}

.adjacent-entry-pagination {
    margin-bottom: 0;
}

.archive-pagination li {
    display: inline;
}

.archive-pagination li a {
    background-color: #f5f5f5;
    color: #333;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 4px;
    padding: 8px 12px;
    text-decoration: none;
}

.archive-pagination li a:focus,
.archive-pagination li a:hover,
.archive-pagination li.active a {
    background-color: #333;
    color: #fff;
}


/* Entry Comments
--------------------------------------------- */

.comment-respond,
.entry-comments,
.entry-pings {
    margin-bottom: 40px;
}

.comment-list li {
    padding: 40px 0 0 30px;
}

.comment-list .depth-1 {
    padding-left: 0;
}

.comment-header {
    margin-bottom: 30px;
}

.comment-content {
    clear: both;
}

.comment-content ul>li {
    list-style-type: disc;
}

.comment-respond input[type="email"],
.comment-respond input[type="text"],
.comment-respond input[type="url"] {
    width: 50%;
}

.comment-respond label {
    display: block;
    margin-right: 12px;
}

.comment-header p {
    margin-bottom: 0;
}

.entry-pings .reply {
    display: none;
}

.comment-form-cookies-consent label {
    display: inline;
    padding-left: 10px;
}


/* Sidebar
---------------------------------------------------------------------------- */

.sidebar {
    font-size: 16px;
    line-height: 1.5;
}

.sidebar .widget {
    margin-bottom: 40px;
}

.sidebar p {
    margin-bottom: 20px;
}


/* Footer Widgets
---------------------------------------------------------------------------- */

.footer-widgets {
    border-top: 1px solid #eee;
    background-color: #fff;
    clear: both;
    padding: 60px 0;
}

.footer-widgets .wrap {
    margin-left: auto;
    margin-right: auto;
}

.footer-widget-area {
    margin-bottom: 40px;
    padding-left: 30px;
    padding-right: 30px;
}

.footer-widget-area:last-child,
.footer-widgets .widget:last-child {
    margin-bottom: 0;
}


/* Site Footer
---------------------------------------------------------------------------- */

.site-footer {
    background-color: #fff;
    border-top: 1px solid #eee;
    font-size: 15px;
    line-height: 1.5;
    padding: 30px;
    text-align: center;
}

.site-footer p {
    margin-bottom: 0;
}